@import url(https://fonts.googleapis.com/css?family=Yrsa:400,500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}.wrapper .wrapper__right p a,.middle__title p:nth-child(3) a{font-weight:bold;color:#ff6429}.wrapper .wrapper__right,footer{height:100vh}.wrapper .wrapper__right h1,.middle__title h1{font-family:"Yrsa";font-size:50px;font-weight:500}.wrapper .wrapper__right p,.middle__title p:nth-child(3){font-family:"Yrsa";font-size:18px;margin-top:30px}.wrapper .wrapper__right,.middle__title{display:flex;flex-direction:column;justify-content:center;align-items:center}footer .footer__upside,footer .footer__downside{display:flex;justify-content:space-around;align-items:center}body{font-family:"Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif}header{position:fixed;z-index:1;top:0;left:0;display:grid;width:100%;grid-template-columns:repeat(3, 1fr);height:60px;padding:10px;align-items:center}header ul{width:100%;display:flex;justify-content:center;align-items:center}header ul li{padding-right:40px;cursor:pointer}header ul li a{color:black;width:100%;text-decoration:none;font-family:"Montserrat", sans-serif}header .header__middle{width:100%;display:flex;justify-content:center}header .header__middle h2{font-size:40px;font-weight:bold;cursor:pointer;display:flex;justify-content:center;align-items:center}header .header__middle h2 div{-webkit-transform:rotateZ(-15deg);transform:rotateZ(-15deg);margin:0 -3px;margin-left:-6px}header .header__search{display:flex;justify-content:center;margin:5px 0}header .header__search i{font-size:30px;cursor:pointer}.wrapper{display:grid;grid-template-columns:1fr 1fr;grid-template-rows:calc(100vh - 60px)}.wrapper .wrapper__photo{background-image:url(https://source.unsplash.com/random/)}.wrapper .wrapper__right{justify-self:center;width:60%;margin:0 100px;text-align:center;line-height:1.2}.middle__title{background-color:#efcb56;padding:150px}.middle__title p{width:65%;font-size:30px;margin:25px 120px;line-height:1.2;text-align:center}.middle__grid{display:grid;grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(4, 1fr);box-sizing:content-box;margin:10px 10px 10px 10px;height:200vh;justify-content:center;align-items:center;grid-gap:10px;gap:10px}.middle__grid .grid__pic{position:relative;width:100%;height:100%}.middle__grid .grid__pic:first-child,.middle__grid .grid__pic:nth-child(5),.middle__grid .grid__pic:nth-child(7),.middle__grid .grid__pic:nth-child(10){grid-column-start:span 2}.middle__grid .grid__pic .grid__pic__bg{width:100%;height:100%;background:url(https://source.unsplash.com/random/);background-size:cover;background-position:center;transition:all 0.2s linear;cursor:pointer;overflow:hidden}.middle__grid .grid__pic .grid__pic__bg:hover{-webkit-filter:blur(50px);filter:blur(50px)}.middle__grid .grid__pic .grid__pic__text{position:absolute;top:50%;right:50%;display:flex;flex-direction:column;justify-content:center;align-items:center}.middle__grid .grid__pic .grid__pic__text span{display:none;margin:5px 0}.middle__grid .grid__pic .grid__pic__text span:first-child{font-weight:bold;font-size:20px}.middle__grid .grid__pic .grid__pic__text span:hover{display:flex}footer{background-color:#535851}footer .footer__upside{height:50vh;padding:150px 150px;width:75%}footer .footer__upside .footer__upside__items{height:100%;display:flex;justify-content:center;font-weight:500}footer .footer__upside .footer__upside__items div{padding:10px 0;color:white}footer .footer__upside .footer__upside__items div div:first-child{color:#a9aba8;font-size:20px}footer .footer__upside .footer__upside__items:last-child{display:flex;flex-direction:column;height:100%;justify-content:stretch}footer .footer__downside{width:65%;color:white}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

